<template>
  <div class="bigbox">
    <v-card class="cardbox">
      <v-card-title>
        <v-icon @click="switchover"
                class="material-icons margin1">
          {{valuestatus?'chevron_right':'expand_more'}}
        </v-icon>
        <span>
          <v-checkbox v-model="allChecked"
                      :label="copylist.table_description"
                      :readonly='readonly'
                      color='#cccccc'
                      :indeterminate='indeterminate'
                      @change="allchecked"
                      class="checkbox"></v-checkbox>
        </span>

      </v-card-title>
      <v-card-text :style="{display:valuestatus?'block':'none'}"
                   class="cardtext">
        <v-row>
          <v-col cols="6"
                 v-for="(item1,index) in copylist.mrbase_sys_datamodel_field "
                 :key="index"
                 class="padding-left">
            <span>
              <v-checkbox :readonly='readonly'
                          color='#cccccc'
                          :true-value="1"
                          :false-value="0"
                          v-model="item1.is_international"
                          :label="item1.field_name"
                          @change="changeindeter"
                          class="checkbox"></v-checkbox>

            </span>
            <span class="f1">{{item1.field_description}}</span>
          </v-col>
        </v-row>
      </v-card-text>

    </v-card>
    <!-- <mrc_ess_submitBtn @success='success'
              @cancel='cancel'
              v-if='!readonly'></mrc_ess_submitBtn> -->
  </div>
</template>

<script>
import { valuedetail, valueupdate } from '@/service/api/internationalization.js'
export default {
  components: {

  },
  props: {
    list: {
      type: Object,
      default: []
    },
    readonlyed: {
      type: Boolean
    }
  },
  data () {
    return {
      readonly: false, //读写状态
      valuestatus: false,
      indeterminate: null, //半选状态
      copylist: []
    }
  },
  created () {
  },
  async mounted () {
    this.readonly = this.readonlyed
    // this.copylist = this.$clone(this.list)
    this.copylist = JSON.parse(JSON.stringify(this.list))
    // console.log(this.list);
    this.changeindeter()
  },
  methods: {
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    getchildrenlist () {
      // console.log(11);this.$set(this.copylist,is_international,1)
      // console.log(this.allChecked, this.allchecked ? this.$set(this.copylist, 'is_international', 1) : this.$set(this.copylist, 'is_international', 0));
      this.allChecked ? this.copylist.is_international = 1 : this.copylist.is_international = 0
      // this.allChecked ? this.$set(this.copylist, 'is_international', 1) : this.$set(this.copylist, 'is_international', 0)
      this.$emit('getchildrenlist', this.copylist)
    },
    success () {  //提交
      console.log(this.copylist);
      // valueupdate({
      //   table_uuids: '',
      //   field_uuids: ''
      // })
    },
    switchover () {
      this.valuestatus = !this.valuestatus
    },
    allchecked () {
      this.getchildrenlist()
    },
    changeindeter () {
      this.getchildrenlist()
      if (this.copylist.mrbase_sys_datamodel_field.length > 0) {
        if (this.copylist.mrbase_sys_datamodel_field.some(item => { //有一个选中就出现
          return item.is_international == 1
        })) {
          // console.log(1, this.indeterminate);
          this.indeterminate = true
        }
        if (this.copylist.mrbase_sys_datamodel_field.every(item => { //全部未就消失
          return item.is_international == 0
        })) {
          // console.log(2);
          this.indeterminate = false
        }
        if (this.copylist.mrbase_sys_datamodel_field.every(item => { //全部选中就消失
          return item.is_international == 1
        })) {
          // console.log(3); 
          this.indeterminate = null
        }
      }
    }
  },
  computed: {
    allChecked: {
      get () { //全选状态
        if (this.copylist.mrbase_sys_datamodel_field) {
          return this.copylist.mrbase_sys_datamodel_field.every(item => {
            return item.is_international == 1
          })
        }
      },
      set (val) {
        val == 1 ? this.indeterminate = null : this.indeterminate = false
        this.copylist.mrbase_sys_datamodel_field.forEach(item => {
          val == 1 ? item.is_international = 1 : item.is_international = 0
        })
      }
    }
  },
  watch: {
    list: {
      handler (val) {
        // this.copylist = val
        this.copylist = JSON.parse(JSON.stringify(val))
      }, deep: true
    },
    readonlyed (v) {
      // console.log(v);
      this.readonly = v
    }
  },
};
</script>

<style scoped>
.bigbox /deep/ .v-label {
  padding-left: 9px;
}
.bigbox /deep/ .v-card__title {
  padding: 0;
  height: 50px;
}
.cardtext /deep/ .v-input__control {
  height: 25px;
}
.padding-left {
  padding: 0 0 0 50px;
}
.f1 {
  color: #cccccc;
  display: block;
  text-align: left;
  padding-left: 40px;
}
.cardbox {
  margin-bottom: 15px;
}
.margin1 {
  margin: 0 15px;
}
</style>
